.paginationSection {
    display: flex;
    justify-content: center;
    margin-top: 20px;

    .prevPage,
    .nextPage {
        position: relative;
        top: 0px;
        padding: 2px 10px 5px;
        background-color: #2876f9;
        background-image: linear-gradient(270deg, #2876f9 0%, #6d17cb 74%);
        color: white;
        border-radius: 2px;
        font-weight: bold;
        cursor: pointer;
        box-shadow: 0px 0px 5px 1px #d6d6d6;
        transition: all 0.3s;

        &:hover {
            background-color: #ff4e00;
            background-image: linear-gradient(315deg, #ff4e00 0%, #ec9f05 74%);
        }
    }
    .currentPage {
        margin: 0px 10px;
        padding: 2px 5px;
        font-weight: bold;
        background: white;
        border-radius: 50%;
    }
}
