.wishlistHeart {
    position: absolute;
    top: 5px;
    right: 5px;
    background: white;
    padding: 2px 6px;
    border-radius: 50%;

    i {
        color: gray;
        cursor: pointer;

        &:hover {
            color: red;
        }
    }

    &.heart i {
        color: red;
    }
}
