.sortFilter {
    position: fixed;
    width: 250px;
    height: 100%;
    padding: 55px 20px 40px;
    overflow: auto;
    background-color: #fff;
    left: 0;
    top: 0;
    z-index: 5;
    box-shadow: rgb(50 50 93 / 25%) 0px 50px 100px -20px, rgb(0 0 0 / 30%) 0px 30px 60px -30px,
        rgb(176 184 192 / 35%) 0px -2px 6px 0px inset;

    .sortList,
    .filterList {
        padding: 10px 0px;
        list-style: none;
        font-weight: bold;

        li {
            font-weight: 500;
            padding: 5px 10px;
            input[type="radio"] {
                display: none;
            }
            label {
                position: relative;
                cursor: pointer;
                &:hover {
                    color: #045de9;
                }
                &::after {
                    content: " ";
                    display: none;
                    position: absolute;
                    top: 6px;
                    right: -15px;
                    width: 8px;
                    border-radius: 50%;
                    height: 8px;
                    background: #045de9;
                }
            }
            input[type="radio"]:checked ~ label {
                color: #045de9;
                &::after {
                    display: block;
                }
            }
            input[type="checkbox"] {
                position: relative;
                top: 2px;
                margin-right: 5px;
            }
            &.minMax {
                padding: 0;
                ul {
                    list-style: none;
                    li {
                        font-weight: 500;
                        label {
                            cursor: default;
                            &:hover {
                                color: black;
                            }
                        }
                        input {
                            padding: 5px;
                            border: 1px solid rgb(180, 180, 180);
                            border-radius: 4px;
                            width: 100%;
                        }
                        .applyBtn {
                            position: relative;
                            top: 0px;
                            border: 0;
                            padding: 7px 10px;
                            background-color: #2876f9;
                            background-image: linear-gradient(270deg, #2876f9 0%, #6d17cb 74%);
                            color: white;
                            border-radius: 2px;
                            font-weight: bold;
                            cursor: pointer;
                            box-shadow: 0px 0px 5px 1px #d6d6d6;
                            transition: all 0.3s;

                            &:hover {
                                background-color: #ff4e00;
                                background-image: linear-gradient(315deg, #ff4e00 0%, #ec9f05 74%);
                            }
                        }
                    }
                }
            }
        }
    }
}
