section.prodpage {
    padding: 20px 0px;
    display: flex;
    .imageContainer {
        display: flex;
        .imageList {
            display: flex;
            flex-direction: column;
            img {
                padding: 5px;
                max-width: 50px;
                cursor: pointer;
                &:hover {
                    border: 2px solid blue;
                }
            }
        }
        .selectedImage {
            flex: 1;
            img {
                max-width: 400px;
            }
        }
    }
    .prodInfo {
        padding: 0px 20px 20px;
        h2.prodTitle {
            color: gray;
            margin-bottom: 10px;
        }
        p.prodDesc {
            font-weight: 500;
            margin-bottom: 10px;
        }
        .pricing {
            display: flex;
            align-items: baseline;
            .presentPrice {
                font-size: 2rem;
                font-weight: 500;
                margin-right: 10px;
                i {
                    font-size: 1.7rem;
                    font-weight: normal;
                }
            }
            .originalPrice {
                font-size: 18px;
                color: gray;
                margin-right: 10px;
                text-decoration: line-through;
            }
            .discount {
                font-size: 18px;
                color: #26a541;
                font-weight: 500;
            }
        }
    }
}
